<template>
    <h3>NameComponent3</h3>

    <fieldset>
        <h4>來自json檔案的資料</h4>
        <ul>
            <li v-for="(p, index) of fromPersonData" :key="index">
                {{ p.name }} ({{ p.age }}) - {{ p.job.j1.salarly }}K
                <ul>
                    <li v-for="(ph, index) of p.hobby" :key="index">{{ ph }}</li>
                </ul>
            </li>
        </ul>

        <ul>
            <li v-for="(s, index) of fromSchoolData" :key="index">{{ s.name }}</li>
        </ul>
    </fieldset>

</template>
<script>
import { toRef } from 'vue'

export default {
    name: 'NameComponent3',
    props: ['personData', 'schoolData'],
    setup(props) {
        console.log('--- NameComponent3 ---')
        
        // 數據
        let fromPersonData = toRef(props, 'personData')
        let fromSchoolData = toRef(props, 'schoolData')

        console.log('fromPersonData', fromPersonData)
        console.log('fromSchoolData', fromSchoolData)

        return {
            fromPersonData,
            fromSchoolData
        }
    }
}
</script>